body {
  font-family: var(--font-family);
  color: var(--text);
  font-size: 1.6rem;
  width: 100%;
}

/* Landscape phones and down */
/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --beige: #d5c2a1;
  --font-family: "Nunito", sans-serif;
  --ff-title: "Great Vibes", cursive;
}

header {
  display: flex;
  align-items: center;
  height: 8rem;
  width: 100%;
  background: var(--beige);
  animation: anim-start 6s;
}

h1 {
  margin-top: 1rem;
  color: white;
  font-family: var(--ff-title);
  font-weight: 200;
  font-size: 4rem;
  animation: opacity 7s;
}

.img-header {
  height: 6rem;
  margin: 0 2rem;
  animation: opacity 7s;
}

footer {
  height: 8rem;
  width: 100%;
  background: var(--beige);
  animation: anim-start 6s;
}

@keyframes anim-start {
  0% {
    height: 50%;
  }
  50% {
    height: 50%;
  }
}
@keyframes text-anim-start-p1 {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes text-anim-start-p2 {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes text-anim-start {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
